body {
  margin: 0px;
}


input[type=text], select, textarea {
  width: 100%;
  padding: 12px 3px;
  border: 2px solid green;
  border-radius: 15px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=date], select, textarea {
  width: 100%;
  padding: 10px 3px;
  border: 2px solid green;
  border-radius: 15px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=submit] {
  color: white;
  cursor: pointer;
  background-color: 	#00FF00;
  padding: 18px;
  border-radius: 15px;
  text-decoration: #00FF00;
  font-size: 16px;
  border: 2px solid 	green;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

input[type=submit]:hover {
  color: 	yellow;
  background-color: 	#00FF00;
  text-decoration: 	#00FF00;
  border: 2px solid 	#00FF00
}
.container {
  border-radius: 5px;
